
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import ecc from 'eosjs-ecc'
import Divider from "../node_modules/vux/src/components/divider/index";
import { eosApi } from './api'
import Clipboard from 'clipboard'

export default {
components: {
Divider,
Group,
Cell
},
data () {
return {
nameError: '',
keyError: 'KEY',
key: '',
name: '',
account: null,
}
},
methods: {
checkPublicKey (value) {
if (ecc.isValidPublic(value)) {
this.keyError = null
} else {
this.keyError = ''
}
},
checkName (value) {
this.account = null;
if (value.length == 0) {
this.nameError = '';
} else if (value.length !== 12){
this.nameError = ' 12 ';
} else if (/[a-z1-5]{12}/.test(value)){
eosApi.getAccount(value).then(function (account) {
this.account = account;
this.nameError = '';
}.bind(this), function (err) {
this.nameError = null;
}.bind(this))
} else {
this.nameError = '';
}
},
copy () {
this.$copyText(this.name+' '+this.key).then(function (e) {
alert('')
}, function (e) {
alert('')
})
}
},
}



